import React, { useState, useEffect } from "react";
import AppRouter from "components/Router";
import { authService } from "fbase";
import { dbService } from "fbase";

function App() {
  const [init, setInit] = useState(false);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [userObj, setUserObj] = useState(null);
  const [card, setCard] = useState(false);
  const [cardData, setCardData] = useState([]);
  const [colName, setColName] = useState("");

  useEffect(() => {
    authService.onAuthStateChanged((user) => {
      if (user) {
        setIsLoggedIn(true);
        setUserObj(user);
      } else {
        setIsLoggedIn(false);
      }
      setInit(true);
    });
  }, []);
  useEffect(() => {
    if (userObj !== null) {
      setColName(userObj.uid.substring(0, 9));
    }
  }, [userObj]);
  useEffect(() => {
    if (colName !== "") {
      dbService.collection(colName).onSnapshot((snapshot) => {
        const colNameArray = snapshot.docs.map((doc) => ({
          ...doc.data(),
          id: doc.id,
        }));

        setCardData(colNameArray);
        if (colNameArray.length !== 0) {
          setCard(true);
        }
      });
    }
  }, [colName]);
  const save = async () => {
    const newStage1 = Array.from(new Set(cardData[0].stage1));
    const newStage2 = Array.from(new Set(cardData[0].stage2));
    const newStage3 = Array.from(new Set(cardData[0].stage3));
    const newStage4 = Array.from(new Set(cardData[0].stage4));
    await dbService.doc(`${colName}/${cardData[0].id}`).update({
      stage1: newStage1,
      stage2: newStage2,
      stage3: newStage3,
      stage4: newStage4,
    });

    alert("저장되었습니다.");
  };

  return (
    <>
      {init ? (
        <AppRouter
          isLoggedIn={isLoggedIn}
          userObj={userObj}
          save={save}
          card={card}
          cardData={cardData[0]}
        />
      ) : (
        "Initializing..."
      )}
      <div className="footer">
        <footer>&copy; {new Date().getFullYear()} 2nd-deck</footer>
      </div>
    </>
  );
}

export default App;
